.main-sidebar {
  @include shadow;
  @include main-sidebar;
  left: 0;
}

.main-sidebar, 
.navbar, 
.main-content, 
.main-footer {
  transition: all .5s;
}

body.sidebar-gone {
  .main-sidebar {
    left: -250px;
  }
}

.sidebar-mini {
  .hide-sidebar-mini {
    display: none !important;
  }
  .main-sidebar {
    width: 65px;
    overflow: initial !important;
    position: absolute;
    box-shadow: none;
    &:after {
      @include shadow;
      content: ' ';
      position: fixed;
      background-color: #fff;
      width: 65px;
      height: 100%;
      left: 0;
      top: 0;
      z-index: -1;
      opacity: 0;
      animation-name: mini-sidebar;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
      @keyframes mini-sidebar {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
    }
    .logo-name {
      display: none;
    }
    .sidebar-user{
      margin: 0;
      height: 70px;
      padding: 15px;
      .sidebar-user-picture{
        img{
          width: 35px;
        }
      }
      .sidebar-user-details{
        .user-name,.user-role{
          display: none;
        }
      }
    }
    .sidebar-brand-sm {
      display: block;
    }
    .sidebar-menu {
      > li {
        padding: 10px;
        &.menu-header {
          padding: 0;
          font-size: 0;
          height: 2px;
        }
        > a {
          border-radius: 3px;
          height: 45px;
          padding: 0;
          justify-content: center;
          .ion, .fas, .far, .fab, .fal {
            margin: 0;
            font-size: 20px;
          }
          span {
            display: none;
          }
          .badge {
            padding: 5px;
            position: absolute;
            top: 4px;
            right: 4px;
            font-size: 10px;
          }
          &.has-dropdown {
            &:after {
              content: initial;
            }
          }
        }
        &.active {
          > a {
            @include icon-shadow(primary);
            background-color: color(primary);
            color: #fff;
          }
        }
        ul.dropdown-menu {
          position: absolute;
          background-color: #fff;
          left: 65px;
          top: 10px;
          width: 200px;
          display: none;
          @include shadow(0 0 30px);
          li > a:focus,
          li.active > a,
          li.active > a:hover {
            color: color(primary);
        /*    background-color: color(primary) !important;*/
          }
          li {
            a {
              height: 40px;
            /*  padding: 0 20px;*/
              background-color: #fff;
              &.has-dropdown {
                &:after {
                  @include dropdown-indicator();
                }
              }
            }
          }
        }
      }
      li:hover {
        > ul.dropdown-menu {
          display: block !important;
          li {
            &:hover {
              > a {
                background-color: color_lighten(light, 7.6%);
              }
            }
            .dropdown-menu {
              left: 200px;
              padding: 0;
            }
          }
        }
      }
    }
  }
  .navbar {
    left: 65px;
  }
  .main-content, .main-footer {
    padding-left: 90px;
  }
}

// common style

.loader {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: url('../img/loading.gif') 50% 50% no-repeat rgb(249,249,249);
    opacity: 1;
}
.user-img-radious-style{
    border-radius: 6px;
    box-shadow: 4px 3px 6px 0 rgba(0, 0, 0, 0.2);
}
.shadow-style {
  -webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

body.layout-2 {
  .navbar-bg {
    z-index: 889;
    height: 70px;
  }
  .navbar {
    left: 0;
    z-index: 890;
  }
  .main-wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 0 50px;
  }
  .main-sidebar, 
  .main-content, 
  .main-footer {
    flex-shrink: 0;
    flex-grow: 0;    
  }
  .main-sidebar {
    background-color: transparent;
    box-shadow: none;
    position: static;
    margin-top: 100px;
    width: 200px;
    .sidebar-menu {
      li {
        &.menu-header {
          padding: 0;
        }
        a {
          padding: 0;
          i {
            width: 10px;
          }
          &.has-dropdown {
            &:after {
              right: 0;
            }
          }
          &:hover {
            color: color(primary);
            background-color: transparent;
          }
        }
        ul.dropdown-menu {
          li {
            a {
              padding-left: 34px;
            }
          }
        }
      }
    }
  }
  .main-content {
    padding-top: 107px;
    padding-left: 30px;
    padding-right: 0;
    width: calc(100% - 200px);
  }
  .main-footer {
    margin-left: 230px;
    width: calc(100% - 230px);
    padding-left: 0;
    padding-right: 0;
  }
}

body.layout-3 {
  .navbar {
    left: 0;
    right: 0;
  }
  .navbar.navbar-secondary {
    @include shadow;
    background-color: #fff;
    top: 70px;
    padding: 0;
    z-index: 889;
    .navbar-nav {
      > .nav-item {
        &.active {
          > .nav-link {
            color: color(primary);
            &:before {
              left: 35px;
              right: 0;
            }
          }
        }
        &:first-child {
          .nav-link {
            margin-left: 0;
          }
        }
        &:last-child {
          .nav-link {
            margin-right: 0;
          }
        }
        > .nav-link {
          color: color_lighten(font, 10%);
          font-size: 13px;
          letter-spacing: .3px;
          height: 70px;
          padding: 0;
          padding-left: 0 !important;
          padding-right: 0 !important;
          margin-left: 15px;
          margin-right: 15px;
          position: relative;
          &.has-dropdown {
            margin-right: 35px;
            &:after {
              @include dropdown-indicator;
              right: -20px;
            }
          }
          &:before {
            content: ' ';
            position: absolute;
            left: initial;
            right: initial;
            bottom: 0;
            height: 2px;
            background-color: color(primary);
            transition: all .5s;
          }
          span {
            line-height: 74px;
          }
          i {
            width: 30px;
            font-size: 16px;
          }
          &:hover {
            color: color(dark) !important;
          }
        }
        .dropdown-menu {
          padding: 0;
          .nav-item {
            .nav-link {
              color: color(font);
              font-weight: 600;
              letter-spacing: .3px;
              padding: 7px !important;
              padding-left: 20px !important;
              padding-right: 20px !important;
              &.has-dropdown {
                &:after {
                  @include dropdown-indicator();
                  right: 15px;
                }
              }
            }
            &:hover {
              > .nav-link {
                background-color: color_lighten(light, 7.6%);
                color: color(dark);
              }
              > .dropdown-menu {
                display: block !important;
                top: -5px;
                left: 200px;
              }
            }
          }
          .nav-item.active > .nav-link,
          .nav-item .nav-link:focus {
            background-color: color(primary);
            color: #fff;
          }
        }
      }
    }
  }
  .main-content {
    padding-left: 0;
    padding-right: 0;
    padding-top: 170px;
  }
  .main-footer {
    padding-left: 0;
    padding-right: 0;
  }
}

.main-sidebar {
  .sidebar-brand {
    display: inline-block;
    width: 100%;
    text-align: center;
    height: 70px;
    line-height: 70px;
    &.sidebar-brand-sm {
      display: none;
    }
    a {
      text-decoration: none;
      text-transform: uppercase;
      letter-spacing: 3px;
      font-weight: 700;
      font-size: 23px;
      vertical-align: bottom;
      color: #000;
      .header-logo{
      	height: 25px;
      }
      .logo-name{
        vertical-align: middle;
        font-size: 20px;
      }
    }
  }
  .sidebar-user {
    display: inline-block;
    width: 100%;
    padding: 10px;
    text-align: center;
    .sidebar-user-picture {
    //  float: left;
      margin-right: 10px;
      img {
        width: 60px;
        border-radius: 20%;
      }
    }
    .sidebar-user-details{
	     .user-name {
		    white-space: nowrap;
		    -o-text-overflow: ellipsis;
		    text-overflow: ellipsis;
		    overflow: hidden;
		    margin-top: 7px;
		    margin-bottom: 3px;
		    font-weight: 600;
		    color: #505c66;
		}
		.user-role{
			font-weight: 400;
		    color: #868e96;
		    font-size: 10px;
		    letter-spacing: .5px;
		}
	}
  }
  .sidebar-menu {
    padding: 0;
    margin: 0;
    li {
      display: block;
      &.menu-header {
        padding: 3px 15px;
        color: color_lighten(font, 10%);
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: 1.3px;
        font-weight: 600;
        &:not(:first-child) {
          margin-top: 10px;
        }
      }
      a {
        position: relative;
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 20px;
        width: 100%;
        letter-spacing: .3px;
        color: color_darken(font, 5%);
        font-weight: 600;
        text-decoration: none;
        .badge {
          float: right;
          padding: 5px 10px;
          margin-top: 2px;
        }
        i {
          width: 28px;
          font-size: 15px;
          margin-right: 10px;
          text-align: center;
        }
        span {
          margin-top: 3px;
          width: 100%;
        }
        &:hover {
          background-color: color_lighten(light, 7.6%);
        }
      }
      &.active {
        a {
          color: color(primary);
          font-weight: 600;
          background-color: color_lighten(light, 7.6%);
        }
        ul.dropdown-menu {
          background-color: color_lighten(light, 7.6%);
        }
      }
      a.has-dropdown {
        &:after {
          @include dropdown-indicator();
        }
      }
      &.active > ul.dropdown-menu {
        display: block;
        li {
          a {
            &:hover {
              background-color: color_lighten(light, 7.6%);
            }
          }
        }
      }
      ul.dropdown-menu {
        padding: 0;
        margin: 0;
        display: none;
        position: static;
        float: none;
        width: 100%;
        box-shadow: none;
        background-color: transparent;
        li {
          a {
            color: color_darken(font, 5%);
            height: 35px;
            padding-left: 55px;
            font-weight: 400;
            &:hover {
              color: color(primary);
              background-color: inherit;
              &:before {
              	color: color(primary);
            	font-weight: 600;
            	left: 35px;
            }
            }
            &:before {
              content: '\f30b';
              font-family: 'Font Awesome 5 Free';
              font-weight: 900;
              font-size: 12px;
              position: absolute;
              transition: .5s;
              left: 30px;
              color: #868e96;
            }
          }
          &.active > a {
            color: color(primary);
            font-weight: 600;
            &:before {
              color: color(primary);
            	font-weight: 600;
            }
          }
          a i {
            margin-top: 1px;
            text-align: center;
          }
          ul.dropdown-menu {
            padding-left: 10px;
          }
        }
      }
    }
  }
}

