// Style
body:not(.sidebar-mini) .sidebar-style-1 {
  .sidebar-menu {
    li {
      &.active {
        a {
          background-color: color(primary);
          color: #fff;
        }
        ul {
          &.dropdown-menu {
            li {
              a {
                color: color_lighten(primary, 28%);
                &:hover {
                  background-color: color(primary);
                  color: #fff;
                }
              }
              &.active {
                a {
                  color: #fff;
                }
              }
            }
          }
        }
      }
    }
  }
}

body:not(.sidebar-mini) .sidebar-style-2 {
  .sidebar-menu {
    > li {
      &.active {
        > a {
          padding-left: 20px;
          position: relative;
          &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            height: 25px;
            width: 4px;
        /*    background-color: #fff;*/
          }
        }
      }
    }
    li {
      &.active {
        ul {
          &.dropdown-menu {
            li {
              a {
              /*  padding-left: 61px;*/
                background-color: #fff;
              }
            }
          }
        }
      }
    }
  }
}

.theme-setting {
	position: fixed;
	bottom: 20px;
	right: 20px;
	z-index: 2002;
	.theme-setting-toggle {
		transition: all .5s;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: #f73f52;
		color: #fff;
		box-shadow: 0 10px 30px rgba(0, 0, 0, .2);
		text-align: center;
		line-height: 60px;
		cursor: pointer;
		i {
			font-size: 24px;
		}
	}
	.theme-setting-options {
		transition: all .5s;
		transition-delay: .3s;
		z-index: -1;
		position: absolute;
		left: -220px;
		bottom: 0;
		height: 150px;
		width: 50px;
		background-color: #e8e6e6;
		box-shadow: 0 0 40px rgba(0, 0, 0, 0.05);
		border-radius: 12px;
		visibility: hidden;
		opacity: 0;
		ul {
			padding: 0;
			margin: 0;
			width: 100%;
			display: inline-block;
			margin-left: 20px;
			li {
				width: 20px;
				height: 20px;
				background-color: #000;
				margin-right: 10px;
				margin-top: 15px;
				border-radius: 3px;
				display: inline-block;
				cursor: pointer;
				opacity: 0;
				transition: all .5s;
				&:hover {
					opacity: .8;
				}
			}
		}
	}
	&.active {
		.theme-setting-toggle {
			margin: 5px;
			box-shadow: none;
			line-height: 50px;
			width: 40px;
			height: 40px;
			transform: rotate(90deg);
		}
		.theme-setting-options {
			visibility: visible;
			opacity: 1;
			width: 220px;
			ul li {
				opacity: 1;
				transition-delay: .3s;
			}
		}
	}
}

@media (max-width: 1024px) {
  .sidebar-gone-hide {
    display: none !important;
  }
  .sidebar-gone-show {
    display: block !important;
  }
  .main-sidebar {
    position: fixed !important;
    margin-top: 0 !important;
    z-index: 891;
  }
  body.layout-2,
  body.layout-3 {  
    .main-wrapper {
      width: 100%;
      padding: 0;
      display: block;
    }
  }
  .main-content {
    padding-left: 30px;
    padding-right: 30px;
    width: 100% !important;
  }
  .main-footer {
    padding-left: 30px;
  }
  body {
    &.search-show {
      .navbar {
        z-index: 892;
      }
      overflow: hidden;
    }
    &.sidebar-show {
      overflow: hidden;
    }
    &.search-show:before, &.sidebar-show:before {
      content: '';
      position: fixed;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0;
      z-index: 891;
      -webkit-animation-name: fadeinbackdrop;
              animation-name: fadeinbackdrop;
      -webkit-animation-duration: 1s;
              animation-duration: 1s;
      -webkit-animation-fill-mode: forwards;
              animation-fill-mode: forwards;
    }
    @-webkit-keyframes fadeinbackdrop {
      to {
        opacity: .6;
      }
    }
    @keyframes fadeinbackdrop {
      to {
        opacity: .6;
      }
    }
  }
}